.Button {
    color: #394b56;
    width: 30px;
    height: auto;
    margin: 5px;
    padding: 5px;
    border-radius: 5px;
    background-color: #C0D6DF;
    border: 2px solid #394b56;
    font-size: 20px;
}

.ButtonReset {
    height: auto;
    margin: 5px;
    border: 2px solid #394b56;
    border-radius: 5px;
    font-size: 20px;
    color: #394b56;
    background-color: #88A2AD;
    width: 100%;

    /*padding: 5px;*/
}

.ButtonErrorDecrease {
    height: auto;
    border: 2px solid #394b56;
    border-radius: 5px;
    font-size: 20px;
    color: #394b56;
    background-color: #88A2AD;
    opacity: 0.5;
}

.ButtonErrorIncrease {
    height: auto;
    border: 2px solid #394b56;
    border-radius: 5px;
    font-size: 20px;
    color: #394b56;
    background-color: #88A2AD;
    opacity: 0.5;
}

